import './pages/index.css';
import { createCard } from './components/card.js';
import { openPopup, closePopup } from './components/modal.js';
import { enableValidation, clearValidation } from './components/validate.js';
import { getUserId, getCards, editProfileData, addNewCard, refreshAvatar } from './components/api';

const profile = document.querySelector('.profile');
const profileContainer = profile.querySelector('.profile__bio');
const nameProfile = profileContainer.querySelector('.profile__name'); //   
const jobProfile = profileContainer.querySelector('.profile__about'); //   
const buttonAddInfo = document.querySelector('.profile__edit-button'); //     
const profilePopup = document.querySelector('.popup_profile-edit'); //   
const cardPopup = document.querySelector('.popup_element-edit'); //   
const cardButtonAdd = document.querySelector('.profile__add-button'); //     
const formUserAddInfo = document.querySelector('.popup__userAddInfo'); //    
const nameInput = formUserAddInfo.querySelector('.popup__input_data_name'); //   
const jobInput = formUserAddInfo.querySelector('.popup__input_data_about'); //   
const cardBox = document.querySelector('.elements'); //  
const formUserAddCard = document.querySelector('.popup__cardAdd'); //    
const titleInputCard = document.querySelector('.popup__input_data_title'); //    
const photoInputCard = document.querySelector('.popup__input_data_link'); //   
const saveProfileButton = document.querySelector('.popup__saveProfile'); //     
const saveCardButton = document.querySelector('.popup__saveCard'); //    
const refreshButtonAvatar = document.querySelector('.profile__btn-refresh-avatar'); //   
const refreshAvatarPopup = document.querySelector('.popup__refresh-avatar'); //   
const inputAvatar = document.querySelector('.popup__input_data_link-avatar'); //     
const saveAvatarButton = document.querySelector('.popup__saveAvatar'); //   
const profileAvatar = document.querySelector('.profile__avatar'); //  

export const userId = {}

const user = {}

const newCard = { owner: {} };

Promise.allSettled([getUserId(), getCards()])
  .then(([{ value: user }, { value: cards }]) => {
    nameProfile.textContent = user?.name;
    jobProfile.textContent = user?.about;
    profileAvatar.src = user?.avatar;
    userId._id = user?._id;
    fillCards(cards)
  })
  .catch(err => { console.log(err) })

//  Popup  - Profile:
buttonAddInfo.addEventListener('click', () => {
  openPopup(profilePopup);
  clearValidation(profilePopup);
  nameInput.value = nameProfile.textContent;
  jobInput.value = jobProfile.textContent;
});

formUserAddInfo.addEventListener('submit', submitProfileForm);

//     Popup  - Profile:
function submitProfileForm(evt) {
  evt.preventDefault();
  user.name = nameInput.value;
  user.about = jobInput.value;
  saveProfileButton.textContent = '...';
  editProfileData(user)
    .then((user) => {
      nameProfile.textContent = user.name;
      jobProfile.textContent = user.about;
      closePopup(profilePopup);
    })
    .catch(err => console.log(err))
    .finally(() => saveProfileButton.textContent = '')
}

//  Popup  - Element:
cardButtonAdd.addEventListener('click', () => {
  openPopup(cardPopup);
  clearValidation(cardPopup);
});

//     Popup  - Element:
function submitCardForm(evt) {
  evt.preventDefault();
  newCard.name = titleInputCard.value;
  newCard.link = photoInputCard.value;
  saveCardButton.textContent = '...';
  addNewCard(newCard)
    .then((card) => {
      const cardBoxChild = cardBox.querySelector('.elements__wrapper');
      cardBoxChild.prepend(createCard(card));
      removeLastElement();
      closePopup(cardPopup);
    })
    .catch(err => console.log(err))
    .finally(() => {
      saveCardButton.textContent = '';
      formUserAddCard.reset();
    })
}

formUserAddCard.addEventListener('submit', submitCardForm);

//  Popup  - Refresh Avatar:
refreshButtonAvatar.addEventListener('click', () => {
  openPopup(refreshAvatarPopup);
  clearValidation(refreshAvatarPopup);
})

//     Popup  - Refresh Avatar:
function submitrefreshAvatar(evt) {
  evt.preventDefault();
  saveAvatarButton.textContent = '...';
  refreshAvatar(inputAvatar.value)
    .then((data) => {
      profileAvatar.src = data.avatar;
      closePopup(refreshAvatarPopup);
    })
    .catch(err => console.log(err))
    .finally(() => saveProfileButton.textContent = '')
}

refreshAvatarPopup.addEventListener('submit', submitrefreshAvatar)

function removeLastElement() {
  const cards = cardBox.querySelectorAll('.element');
  cards[cards.length - 1].remove();
}

export function fillCards(cards) {
  const cardsHtml = document.createElement('div')
  cardsHtml.classList.add('elements__wrapper');
  cards.forEach(card => cardsHtml.append(createCard(card)))
  const cardBoxChild = cardBox.querySelector('.elements__wrapper');
  cardBox.replaceChild(cardsHtml, cardBoxChild);
}

enableValidation();
